.bard {
  margin: auto;
  z-index: 4;
  height: 59px;
  width: 35px;
  position: absolute;
  background-size: 35px 59px;
  top: 140px;
  left: 85px;
  -moz-animation: perform infinite 1.5s;
  -webkit-animation: perform infinite 1.5s;
  animation: perform infinite 1.5s;
  animation-timing-function: steps(5, end);
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
}

.bard:hover {
  cursor: pointer;
}

@-moz-keyframes perform {
  0% {
    background: url(./images/bard/bard-1.png) no-repeat;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    background-size: 35px 59px;
  }
  24.9% {
    background: url(./images/bard/bard-1.png) no-repeat;
